/**
 * @file Bring in fonts.
 */

/**
 * Paytone One - For "Branding" font.
 *
 * Defaults to weight 400 with all styles included.
 */
@import "../../~@fontsource/paytone-one/index.css";

/**
 * Open Sans - Sans serif font.
 *
 * Defaults to weight 400 with all styles included.
 */
 @import "../../~@fontsource/open-sans/index.css";

/**
 * LCD font.
 *
 * @link https://www.1001fonts.com/digital-7-font.html
 */
@font-face {
	font-family: 'Digital-7';
	font-style: normal;
	font-weight: 400;
	src: local(''),
	url('../fonts/digital-7/digital-7.mono.ttf') format('truetype'), /* Safari, Android, iOS */
}

/**
 * LCD italic font.
 *
 * @link https://www.1001fonts.com/digital-7-font.html
 */
@font-face {
	font-family: 'Digital-7-Italic';
	font-style: normal;
	font-weight: 400;
	src: local(''),
	url('../fonts/digital-7/digital-7.monoitalic.ttf') format('truetype'), /* Safari, Android, iOS */
}
